.celebbox {
  background-color: #AECEFE;
  width: 100%;
  height: 13vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  border-radius: 50px;
  position: relative;
  
}

img {
  height: 90px;
  width: 90px;
  border-radius: 50%;
  border: 3px solid black;
  margin: 0px 10px;
}

.celebbox-h1 {
  display: inline-block;
  margin: auto
}



